.App {
  text-align: center;
}

.App-header {
  background-color: #34bbdf;
  /* height: -webkit-fill-available; */
  height:100vh ;
  padding-top: 150px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  line-height: 1.6 ;
}

.App-intro {
  font-size: large;
}
.myp{
  color:blue ;
  padding-left: 10%;
}
.my_button{
  padding: 12px 25px 12px 25px;
  border: 0;
  background-color: #2196f3;
  border-radius: 6%;
}
.my-input{
  border-color: #e7e7e7;
  padding: 10px;
  margin-right: 5px;
  border-radius: 5%;

}
.center{
  color:#2196f3 ; 
  text-align: center ;
}
.navbox{
  background-color: white ; 
  height: 30px ; 

}
/* to do list view */
li{
color:#333 ; 
background-color:rgba(255,255,255,.5);
padding:15px ;
list-style: none ;
border-radius:5px;
margin-bottom:15px;
padding-left :0px;
padding-top: 10px ;
width:250px;
margin:20px auto ;
}
li:hover {
  background-color:red ;
} 


